.gameCanvasWrapper{
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

.gameCanvasWrapper .title{
	font-size: 30px;
	margin: 10px;
}

.gameCanvasWrapper .playAgain{
	display: none;
	position: absolute;
}

.gameCanvasWrapper canvas.loose ~ .playAgain{
	display: block;
}

canvas{
	background: #66bb6a;
	border: 2px solid #000;
	position: relative;
}

canvas.activeEffect{
	border: 2px solid #000;
	-webkit-animation: .5s linear borderAnim infinite;
	        animation: .5s linear borderAnim infinite;
}

@-webkit-keyframes borderAnim{
	0%{ border-style: dotted; }
	50%{ border-style: dashed; }
	100%{ border-style: dotted; }
}

@keyframes borderAnim{
	0%{ border-style: dotted; }
	50%{ border-style: dashed; }
	100%{ border-style: dotted; }
}

.gameCanvasWrapper .fps{
	position: absolute;
	bottom: 0;
	right: 7px;
	opacity: .5;
}

.gameCanvasWrapper .settingsWrapper{
	position: fixed;
	bottom: 0;
	left: 0;
	display: none;
	-ms-flex-direction: row-reverse;
	    flex-direction: row-reverse;
	-ms-flex-align: end;
	    align-items: flex-end;
	-ms-flex-pack: center;
	    justify-content: center;
}

.gameCanvasWrapper canvas.loose ~ .settingsWrapper{
	display: -ms-flexbox;
	display: flex;
}

.gameCanvasWrapper .settingsWrapper .settingsIcon:before{
	content: '\2699';
	cursor: pointer;
	font-size: 23px;
	padding: 5px;
	-webkit-transition: all .5s ease;
	-o-transition: all .5s ease;
	transition: all .5s ease;
}

.gameCanvasWrapper .settingsWrapper .settingsContent.active + .settingsIcon:before{
	-webkit-transform: rotateZ(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotateZ(-90deg);
}

.gameCanvasWrapper .settingsWrapper .settingsContent{
	display: block;
	opacity: 0;
	-webkit-transition: all .5s ease;
	-o-transition: all .5s ease;
	transition: all .5s ease;
	position: fixed;
	bottom: -10%;
	left: 30px;
	background: #cbae82;
	padding: 0 5px;
	margin: 5px;
	border: #ffffe4 solid 2px;
	text-align: center;
}

.gameCanvasWrapper .settingsWrapper .settingsContent.active{
	bottom: 0;
	opacity: 1;
}

.gameCanvasWrapper .settingsWrapper .settingsContent input,
.gameCanvasWrapper .settingsWrapper .settingsContent select{
	border-radius: 12px;
	border: none;
	padding: 5px 10px;
	margin: 10px;
}

.gameCanvasWrapper .settingsWrapper .settingsContent select{
	border-radius: unset;
}

.gameCanvasWrapper .settingsWrapper .settingsContent div{
	border-bottom: 1px dotted #ffffe4;
}

.gameCanvasWrapper .settingsWrapper .settingsContent  div:last-child{
	border-bottom: none;
}


